/// brand color
$color-brand: #e64980 !default;
$color-brand-light: #f0c7d4 !default;
// $color-brand-medium: #ff6b95 !default;
$color-brand-medium: #ccc !default;
/// primary color
$color-primary: #0275d8 !default;
/// sucess color
$color-success: #5cb85c !default;
/// info color
$color-info: #5bc0de !default;
