// @import '../../../main'; // show main.scss > // 5. Components
/*! src/scss/components/atoms/button/_button.scss */
.btn {
  transition: background-color 0.3s;
  border: 0;
  border-radius: 6px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;

  &--primary {
    background: $color-brand;
  }

  &--primary:hover {
    background-color: #000;
  }

  &--small {
    height: 3.8rem;
  }

  &--medium {
    height: 4.8rem;
  }

  &--pad-small {
    padding: 0 0.8rem;
  }

  &--pad-medium {
    padding: 0 1.6rem;
  }

  &__link {
    // composes: btn; // compose === local @extend
    display: flex;
    align-items: center; /* Vertical align */
    justify-content: center; /* horizontal align */
    width: max-content;
  }
}
