.header {
  display: flex;
  flex: 1 1 0%;
  width: 100%;
  max-width: 115.2rem;
  margin: 0 auto;
}

.header__menu-mobil {
  display: none;
}

.header__logo {
  // flex: 0 0 180px; // for svg
  flex: 0 0 auto;
  align-self: center;
  margin: 0;

  .logo {
    display: flex;
    flex-direction: column;
    text-decoration: none;
  }

  // .logo svg {
  //   width: 18rem;
  //   height: 4.8rem;
  // }

  .logo img {
    width: 18rem;
    height: auto;
  }

  .baseline {
    color: $medium-gray;
    font-size: $font-size-small;
    font-weight: normal;
  }
}

.header__search {
  display: flex;
  min-width: 19.2em;
  height: 4.8rem;
  margin: 0 2em;
  margin-left: 8.4em;
  padding: 4px;
  border: 1px solid $light-gray;
  border-radius: 0.6rem;

  > input {
    width: 40rem;
    max-width: 100%;
    height: 100%;
    border: 0;

    &:active,
    &:focus {
      background-color: #eee; // to rm for prod
    }
  }

  > button {
    flex: 0 0 4.7rem;
  }
}

.quickorder {
  display: flex;
  flex: 0 0 18rem;
  margin: 0 auto;
  padding: 0 1.6rem;
  font-size: $font-size-small2;
  font-weight: normal;
  text-align: left;

  > svg {
    margin-right: 0.8em;
    fill: $color-brand-light;
  }

  > svg,
  > span {
    align-self: center;
  }
}

.header__account {
  flex-basis: 8.4rem;
  flex-shrink: 0;

  .icon {
    width: 3.4em;
    height: 3.4em;
  }

  .icon--primary-gradient {
    fill: $color-brand; // replace with svg gradient
    fill: url("#colBrandToColBrandMed");
  }
}

@media (max-width: 700px) {
  .header {
    flex-wrap: wrap;

    &__search {
      flex-basis: 100%;
      order: 1;
      margin: 0;
      margin-top: 3.8rem;
    }
  }

  .header__search > input {
    flex-basis: 100%;
  }
}
@media (max-width: 600px) {
  .header__menu-mobil {
    display: flex;
    flex: 0 0 3rem;
    align-self: stretch;
    margin-right: 1.6rem;
    background-color: #ccc;
  }

  .header__logo {
    margin-right: auto;

    > .logo > img {
      // align-self: center;
      width: 104px;
    }

    > .logo > span {
      display: none;
    }
  }

  .quickorder {
    flex-basis: 100%;
    order: 2;
    margin-top: 1.6rem;
  }

  .header__search {
    margin-top: 1.6rem;
  }

  .header__account {
    flex-basis: 7.2rem;
  }
}
