// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables.
// -----------------------------------------------------------------------------

/// Regular font family
/// @type List
// $font-family-base: "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif !default;
$font-family-base: Arial, "Helvetica Neue", Helvetica, sans-serif !default;

/// Heading font family
/// @type List
// $font-family-headings: "Shadows Into Light", $font-family-base;
@import url("https://fonts.googleapis.com/css?family=Abril+Fatface&display=swap");
$font-family-headings: "Abril Fatface", cursive, $font-family-base;

/// font size
$font-size-base: 1.6rem !default; //16px = 1.6rem
$font-size-small: 1.2rem;
$font-size-small2: 1.3rem;

//Grid
$cols: 12 !default;

//Gutters
// gutter values for grid layouts. Unit can be: %, px, em, rem
$grid-gutters: (
  "": 1rem,
  "-l": 2rem,
  "-xl": 4rem,
);

/// Breakpoints
$small: 320px !default; // or 'em' if you prefer, of course
$medium: 768px !default;
$large: 1152px !default;

/// ex. @include respond-to("medium-up") {...}
$bp-aliases: (
  "small": (
    max-width: #{$small - 1},
  ),
  "medium": (
    max-width: #{$medium - 1},
  ),
  "large": (
    max-width: #{$large - 1},
  ),
  "small-up": (
    min-width: #{$small},
  ),
  "medium-up": (
    min-width: #{$medium},
  ),
  "large-up": (
    min-width: #{$large},
  ),
  "retina": (
    min-resolution: 2dppx,
  ),
) !default;
